body {
  background: red;
}
body div {
  color: white;
  width: calc(100vw - 1.25rem);
}
body div:hover, body div:focus, body div.xy {
  color: green;
  padding: 10px;
  padding-bottom: 20px;
}
body div:after {
  bottom: 10px;
}

.w-1 {
  width: calc(1 * 10%);
}

